* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	color: #555;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

nav {
	display: -webkit-flex;
	display: flex;
	margin-right: -15px;
}

nav a {
	margin: 0 15px;
}

header {
	position: absolute;
	width: 100vw;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
	padding: 35px 100px 0;
	-webkit-animation: 1s fadein 0.5s forwards;
	        animation: 1s fadein 0.5s forwards;
	opacity: 0;
	color: #fff;
}

header h2 {
	font-family: 'Quicksand', sans-serif;
}

@-webkit-keyframes fadein {
	100% {
		opacity: 1;
	}
}

@keyframes fadein {
	100% {
		opacity: 1;
	}
}

@media (max-width: 800px) {
	header {
		padding: 20px 50px;
		-webkit-flex-direction: column;
		        flex-direction: column;
	}

	header h2 {
		margin-bottom: 15px;
	}
}

.hero {
	position: relative;
	-webkit-justify-content: center;
	        justify-content: center;
	text-align: center;
	min-height: 100vh;
	color: #fff;
}

.hero .background-image {
	background-image: url(/lovable-imaginary-codec/static/media/main.3b644ba4.jpg);
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-size: cover;
	z-index: -1;
	/* so you won't see a white flicker when the image loads */
	background-color: #80a3db;
}

.hero h1 {
	font: bold 60px 'Open Sans', sans-serif;
	margin-bottom: 15px;
}

.hero h3 {
	font: normal 28px 'Open Sans', sans-serif;
	margin-bottom: 40px;
}

.hero a.btn {
	padding: 20px 46px;
}

.hero-content-area {
	opacity: 0;
	margin-top: 100px;
	-webkit-animation: 1s slidefade 1s forwards;
	        animation: 1s slidefade 1s forwards;
}

@-webkit-keyframes slidefade {
	100% {
		opacity: 1;
		margin: 0;
	}
}

@keyframes slidefade {
	100% {
		opacity: 1;
		margin: 0;
	}
}

@media (max-width: 800px) {
	.hero {
		min-height: 600px;
	}

	.hero h1 {
		font-size: 48px;
	}

	.hero h3 {
		font-size: 24px;
	}

	.hero a.btn {
		padding: 15px 40px;
	}
}

.destinations .grid li {
	height: 350px;
	padding: 20px;
	background-clip: content-box;
	background-size: cover;
	background-position: center;
}

.destinations .grid li.small {
	-webkit-flex-basis: 30%;
	        flex-basis: 30%;
}

.destinations .grid li.large {
	-webkit-flex-basis: 70%;
	        flex-basis: 70%;
}

@media (max-width: 1100px) {
	.destinations .grid li.small,
	.destinations .grid li.large {
		-webkit-flex-basis: 50%;
		        flex-basis: 50%;
	}
}
@media (max-width: 800px) {
	.destinations .grid li.small,
	.destinations .grid li.large {
		-webkit-flex-basis: 100%;
		        flex-basis: 100%;
	}
}

.packages .grid li {
	padding: 50px;
	-webkit-flex-basis: 50%;
	        flex-basis: 50%;
	text-align: center;
}

.packages .grid li i {
	color: #8c9eff;
}

.packages .grid li h4 {
	font-size: 30px;
	margin: 25px 0;
}

@media (max-width: 800px) {
	.packages .grid li {
		-webkit-flex-basis: 100%;
		        flex-basis: 100%;
		padding: 20px;
	}
}

.quote {
	font-size: 22px;
	font-weight: 300;
	line-height: 1.5;
	margin: 40px 0 25px;
}

@media (max-width: 800px) {
	.quote {
		font-size: 18px;
		margin: 15px 0;
	}

	.author {
		font-size: 14px;
	}
}

.contact form {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	width: 60%;
}

.contact form .btn {
	padding: 18px 42px;
}

.contact form input {
	padding: 15px;
	margin-right: 30px;
	font-size: 18px;
	color: #555;
	-webkit-flex: 1 1;
	        flex: 1 1;
}

@media (max-width: 1000px) {
	.contact form input {
		-webkit-flex-basis: 100%;
		        flex-basis: 100%;
		margin: 0 0 20px 0;
	}
}

footer {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	background-color: #333333;
	color: #ffffff;
	padding: 20px 0;
}

footer ul {
	display: -webkit-flex;
	display: flex;
}

footer ul li {
	margin-left: 16px;
}

footer p {
	text-transform: uppercase;
	font-size: 14px;
	opacity: 0.6;
	-webkit-align-self: center;
	        align-self: center;
}

@media (max-width: 1100px) {
	footer {
		-webkit-flex-direction: column;
		        flex-direction: column;
	}

	footer p {
		text-align: center;
		margin-bottom: 20px;
	}

	footer ul li {
		margin: 0 8px;
	}
}

/* global styles */

.App {
	width: 100vw;
}

a {
	text-decoration: none;
	opacity: 0.75;
	color: #fff;
}

a:hover {
	opacity: 1;
}

a.btn {
	border-radius: 4px;
	text-transform: uppercase;
	font-weight: bold;
	text-align: center;
	background-color: #3f51b5;
	opacity: 1;
	/* padding: 1rem; */
}

section {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	padding: 100px 80px;
}

section h3.title {
	text-transform: capitalize;
	font: bold 32px 'Open Sans', sans-serif;
	margin-bottom: 30px;
	text-align: center;
}

section p {
	max-width: 775px;
	line-height: 2;
	padding: 0 20px;
	margin-bottom: 30px;
	text-align: center;
}

/* excluding the hero section, every other section should alternate between a white background and a light gray background   */
section:not(.hero):nth-child(even) {
	background-color: #f5f5f5;
}

ul {
	list-style: none;
}

.grid {
	width: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-justify-content: center;
	        justify-content: center;
}

hr {
	width: 250px;
	height: 3px;
	background-color: #3f51b5;
	border: 0;
	margin-bottom: 50px;
}

/* media queries */
@media (max-width: 800px) {
	section {
		padding: 50px 20px;
	}
}

